.v-aspect-ratio-16-19 {
  overflow: hidden;
  @supports (aspect-ratio: 16/9) {
    aspect-ratio: 16/19;
  }

  @supports not (aspect-ratio: 16/9) {
    // width: calc((100vh - 130px - 70px) * 16 / 19);
    max-width: 500px;
  }
}

.v-aspect-ratio-20-14 {
  overflow: hidden;
  @supports (aspect-ratio: 16/9) {
    aspect-ratio: 20/14;
  }

  @supports not (aspect-ratio: 16/9) {
    // width: calc((100vh - 130px - 70px) * 16 / 19);
    max-width: 500px;
  }
}

.fixed-locomotive {
  .logo-hub {
    display: none;
  }
  &.menu-opened {
    .logo-hub {
      display: block;
    }
  }
  &.vs-b-menu {
    .logo-hub {
      display: block;
    }
  }
}

.vs-b-menu {
  .logo-hub {
    display: block;
  }
}

